.user-header{
    display: flex;
    justify-content: space-between;
}

.admin-card {
    width: 80vw;
    .card-body {
        height: 65vh;
        overflow: scroll;
    }
}

.admin-user {
    table {
        svg {
            width: 100%;
            min-width: 20px;
            min-height: auto;
        }
    }
}

.alter-user-button:hover {
    color: #212529;
    background-color: #e2e6ea;
    border-color: #dae0e5;
    text-decoration: none;
}

.alter-user-button{
    color: #212529;
    background-color: #f8f9fa;
    border-color: #f8f9fa;
    display: inline-block;
    font-weight: 400;
    color: #212529;
    text-align: center;
    vertical-align: middle;
    -webkit-user-select: none;
    user-select: none;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}
